import { Component, OnInit, OnChanges } from '@angular/core';
import { AuthService } from '../services/auth.service';
import { User } from '../services/auth.service';
import { ItemService } from '../services/item.service';
import { HttpClient } from '@angular/common/http';
import { switchMap} from 'rxjs/operators';

import { Observable, of } from 'rxjs';

import { auth } from 'firebase/app';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFirestore, AngularFirestoreDocument } from '@angular/fire/firestore';
import { AnyARecord } from 'dns';
import { element } from '@angular/core/src/render3';


@Component({
  selector: 'app-weather-cards-menu',
  templateUrl: './weather-cards-menu.component.html',
  styleUrls: ['./weather-cards-menu.component.css']
})

export class WeatherCardsMenuComponent implements OnInit {
  items: Array<any> = [];
  apiKey: string = '5cc57e061cd35f51f5fd9d6564258e4d';
  url: string = 'https://api.openweathermap.org/data/2.5/weather?APPID='
  metric: string = '&mode=json&units=metric';

  firebase = require("firebase/app");

  testArrayOfItems: Array<any> = [];
  currentUID: string = this.firebase.auth().currentUser.uid;

  cityName: string;

  constructor(public auth: AuthService, public itemService: ItemService, private http: HttpClient,
      private afAuth: AngularFireAuth,
      private afs: AngularFirestore,
    ) { 
      //// Get items array from firebase
      this.itemService.getItems().subscribe(items => {
        this.items = items
      })
   }

  ngOnInit() {
    console.log("ngOnInit works!");
    console.log(this.currentUID);
    this.getItemsAndWeather();
  }

  ngOnChanges() {
    console.log("ngOnChanges works!");
  }

  async getItemsAndWeather() {
    this.itemService.getItems().subscribe(items => {
      console.log(items);
      items.forEach(element => {
        this.http.get(this.url + this.apiKey + '&q=' + element.cityName + this.metric).subscribe(
          (response) => {

            response[ 'fireID' ] = element.id;
            this.testArrayOfItems.push(response);
            console.log(this.testArrayOfItems);
          }
        );
      })

      // for(let i = 0; i <= items.length; i++ ) {
      //       this.http.get(this.url + this.apiKey + '&q=' + items[i].cityName).subscribe(
      //         (response) => {

      //           this.testArrayOfItems.push(response);
      //           console.log(this.testArrayOfItems);
      //         }
      //       );
      // }
      
    });
  }

  AddACollectionOfCities() {
    this.afs.collection("users").doc(this.currentUID).collection("items").add({
      cityName: this.cityName,
    })
    this.testArrayOfItems = [];
  }

  deleteItem(event, city) {
    this.itemService.deleteItem(city);
    this.testArrayOfItems = [];
  }

}
