import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } 
from '@angular/fire/firestore';
import { item } from './item.module';
import { Observable, of } from 'rxjs';
import { switchMap, map } from 'rxjs/operators';  

export class City {
  cityName: string;
}



@Injectable({
  providedIn: 'root'
})
export class ItemService {
  itemCollection: AngularFirestoreCollection<item>;
  items: Observable<item[]>;
  user: Observable<any>;
  itemDoc: AngularFirestoreDocument<item>;

  firebase = require("firebase/app");

  constructor(public afs: AngularFirestore) {

    // this.items = this.afs.collection('users').doc(this.firebase.auth().currentUser.uid).collection('items').valueChanges();

    this.items = this.afs.collection('users').doc(this.firebase.auth().currentUser.uid).collection('items').snapshotChanges().pipe(map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.data() as item;
        data.id = a.payload.doc.id;
        return data;
      })
    }))

    

  }

  getItems(){
    return this.items;
  }

  deleteItem(city) {
    this.itemDoc = this.afs.doc(`users/${this.firebase.auth().currentUser.uid}/items/${city.fireID}`);
    console.log(city.fireID);
    this.itemDoc.delete();
  }

}
